import React, { Component } from 'react';
export default class Footer extends Component {

  scrollToHome = e => {
    try {
      document.getElementById("home").scrollIntoView({ behavior: 'smooth' });
    } catch (error) {
      console.log('조회된 Portfolio데이터가 없습니다.')
    }
  };

  render() {
    let socialData = this.props.socialData;
    return (
      <footer>
      <div className="row">
        <div className="twelve columns">
          <ul className="social-links">
            {
              socialData && socialData.map((item)=>{
                return(
                  <li key="footer">
                    <a href={item.url} target="_blank" rel="noopener noreferrer">
                    <i className={item.className} />
                    </a>
                  </li>
                )
              })
            }
          </ul>        
        </div>
        <div id="go-top">
            <div className="top-scroll">
              <button className="btn-up" onClick={this.scrollToHome}>
                <i className="icon-up-open"></i>
                </button>
            </div>
        </div>
        <div className="copyright">
            <p> A portfolio site made by ReactJS<br />
              Dev Portfolio ©2020 created by Lee-Hansung</p>
        </div>
      </div>
    </footer>
    );
  }
}