import React, { Component } from 'react';
import { Link } from "react-router-dom"
import { Document, Page, pdfjs  } from 'react-pdf';
pdfjs.GlobalWorkerOptions.workerSrc = "https://cdn.bootcss.com/pdf.js/2.1.266/pdf.worker.js";

export default class PortfolioViewer extends Component {
  state = {
    numPages: null,
    pageNumber: 1,
  }
 
  onDocumentLoadSuccess = ({ numPages }) => {
    this.setState({ numPages });
  }

  onPageUp = () => {
    if(this.state.pageNumber !== this.state.numPages){
      const  pageUp  = this.state.pageNumber + 1
      this.setState({ pageNumber: pageUp });
    }
  }

  onPageDown = () => {
    if(this.state.pageNumber !== 1){
      const  pageDown  = this.state.pageNumber - 1
      this.setState({ pageNumber: pageDown });
    }
    else return
  }

  render() { 
    const { pageNumber, numPages } = this.state;
    const pdfView = this.props.resumeData.pdfView.pdfUrl
    return (
      <div>
        <nav className="port-nav-wrap" >
          <Link to="/"><div className="nav-contents">Go to Back</div></Link>
        </nav>
        <div className="portfolioviewer">
            <div>
              <Document className="pdf-box"
                file={pdfView}
                onLoadSuccess={this.onDocumentLoadSuccess}>
                <Page  pageNumber={pageNumber} />
                <div className="buttons">
                  <button className="color" type="button" onClick={this.onPageDown}>‹</button>
                  <span>Page {pageNumber} of {numPages}</span>
                  <button className="color" type="button" onClick={this.onPageUp}>›</button>
                </div>
              </Document>
            </div>
        </div>
      </div>
    );
  }
}
