import React, { Component } from 'react';
import Typist from 'react-typist';
import 'react-typist/dist/Typist.css';

export default class Header extends Component {
  
  scrollToAbout = e => {
    document.getElementById("about").scrollIntoView({ behavior: 'smooth' });
  };

  render() {
    let headerData = this.props.headerData;
    let socialData = this.props.socialData;
    return (
      <React.Fragment>
        <header id="home">
          <div className="row banner">
            <div className="banner-text">
              <h1 className="responsive-headline"><Typist avgTypingDelay={170} cursor={{ hideWhenDone: true }}>{headerData.title}</Typist></h1>
              <h3 style={{ color: '#fff', fontFamily: 'sans-serif ' }}>
                Name: {headerData.name}<br></br>
                Email: {headerData.email}<br></br>
                Phone: {headerData.phone}
              </h3>
              <br></br>
              <div className="copyright">
              <Typist avgTypingDelay={120} startDelay={6000}>
                <span>{headerData.made}</span>
                <Typist.Delay ms={1000} />
                <Typist.Backspace count={8} delay={150} />
                <span> React </span>
                <Typist.Delay ms={1000} />
                <Typist.Backspace count={35} delay={150} />
                <span>{headerData.made}</span><br/>
                <span>{headerData.created}</span>
              </Typist>
              </div>
              <hr />
              <ul className="social">
                {
                  socialData && socialData.map(item => {
                    return (
                      <li key={item.name}>
                        <a href={item.url} target="_blank" rel="noopener noreferrer"><i className={item.className}></i></a>
                      </li>
                    )
                  })
                }
              </ul>
            </div>
          </div>
          <div className="scrolldown">
            <div id="go-down">
              <button onClick={this.scrollToAbout}>
                <i className="icon-down-circle"></i>
                </button>
            </div>
          </div>
        </header>
      </React.Fragment>
    );
  }
}