import React, { Component } from 'react';
import NavigationBar from '../components/NavigationBar';
import Header from '../components/Header';
import About from '../components/About';
import Portfolio from '../components/Portfolio';
import Skills from '../components/Skills';
import Contact from  '../components/Contact';
import Footer from '../components/Footer';

export default class Home extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
        <div>
            <NavigationBar/>
            <Header headerData={resumeData.header} socialData={resumeData.socialLinks}/>
            <About aboutData={resumeData.about}/>
            <Portfolio portfolioData={resumeData.portfolio}/>
            <Skills skillsData={resumeData.skills}/>
            <Contact contactData={resumeData.contact}/>
            <Footer socialData={resumeData.socialLinks}/>
        </div>
    );
  }
}