import React, { Component } from 'react';

export default class Skills extends Component {

  render() {
    if (this.props.skillsData && this.props.skillsData.length === 0) {
      return null
    } else if (this.props.skillsData === undefined) {
      return null
    }

    const skillList = this.props.skillsData
    const languageData = skillList.filter(item => item.category.includes('Language'));
    const frameworkData = skillList.filter(item => item.category.includes('Framework'));
    const databaseData = skillList.filter(item => item.category.includes('Database'));
    const systemData = skillList.filter(item => item.category.includes('System'));
    const managingData = skillList.filter(item => item.category.includes('Managing'));
    return (
      <section key="skills" id="skills">
        <div className="row skill">
          <div className="three columns header-col">
            <h1><span>Skills</span></h1>
          </div>
          <div className="six columns">
            <div>
              <br></br><br></br>
              {languageData.length !== 0 ? <h4>Language</h4> : null}
              <div className="skill-box">
                {languageData && languageData.map((item) => {
                  return (
                    <div className="skill-pic" key={item.skillId}>
                      <img src={item.imgUrl} alt="" />
                      <div className="darkness" ></div>
                      <div className="btn-plus"><p draggable="false">{item.level}</p></div>
                      <div className="target-name"><p> {item.skillname} </p></div>
                    </div>)
                })}
              </div>
            </div>
            <div>
              <br></br><br></br>
              {frameworkData.length !== 0 ? <h4>Framework</h4> : null}
              <div className="skill-box">
                {frameworkData && frameworkData.map((item) => {
                  return (
                    <div key={item.skillId} className="skill-pic">
                      <img src={item.imgUrl} alt="" />
                      <div className="darkness"></div>
                      <div className="btn-plus"><p draggable="false">{item.level}</p></div>
                      <div className="target-name"><p> {item.skillname} </p></div>
                    </div>)
                })}
              </div>
            </div>
            <div>
              <br></br><br></br>
              {databaseData.length !== 0 ? <h4>Database</h4> : null}
              <div className="skill-box">
                {databaseData && databaseData.map((item) => {
                  return (
                    <div key={item.skillId} className="skill-pic">
                      <img src={item.imgUrl} alt="" />
                      <div className="darkness"></div>
                      <div className="btn-plus"><p draggable="false">{item.level}</p></div>
                      <div className="target-name"><p> {item.skillname} </p></div>
                    </div>)
                })}
              </div>
            </div>
            <div>
              <br></br><br></br>
              {systemData.length !== 0 ? <h4>System</h4> : null}
              <div className="skill-box">
                {systemData && systemData.map((item) => {
                  return (
                    <div key={item.skillId} className="skill-pic">
                      <img src={item.imgUrl} alt="" />
                      <div className="darkness"></div>
                      <div className="btn-plus"><p draggable="false">{item.level}</p></div>
                      <div className="target-name"><p> {item.skillname} </p></div>
                    </div>)
                })}
              </div>
            </div>
            <div>
              <br></br><br></br>
              {managingData.length !== 0 ? <h4>Managing</h4> : null}
              <div className="skill-box">
                {managingData && managingData.map((item) => {
                  return (
                    <div key={item.skillId} className="skill-pic">
                      <img src={item.imgUrl} alt="" />
                      <div className="darkness"></div>
                      <div className="btn-plus"><p draggable="false">{item.level}</p></div>
                      <div className="target-name"><p> {item.skillname} </p></div>
                    </div>)
                })}
              </div>
            </div>
          </div>
          <div className="nine columns main-col">
          </div>
        </div>
      </section>
    );
  }
}