import React, { Component } from 'react';
export default  class Resume extends Component {
  render() {
    let portfolioData = this.props.portfolioData;
    return (
      <section id="portfolio">
        <div className="row work">
            <div className="three columns header-col">
               <h1><span>Portfolio</span></h1>
            </div>
            <div className="nine columns main-col">
              {
                portfolioData && portfolioData.map((item) => {
                  return(
                    <div className="row item portfolio-box" key={item.portfolioId}>
                      <div>
                        <a href={item.gitHub} target="_blank" rel="noopener noreferrer">
                          <img className="portfolio-pic" src={item.imgUrl} alt="" /> 
                        </a>
                      </div>
                      <div className="twelve columns contents-box">
                        <h3>{item.Name}</h3>
                        <p className="info">-{item.name}</p>
                        <p>
                          <span>&bull;</span>설명: {item.description}<br/>
                          <span>&bull;</span>제작 기간: <em className="tag">{item.period}</em> <br/>
                          <span>&bull;</span>사용 기술: <em className="tag">{item.technology}</em> <br/>
                          <span>&bull;</span>YouTube:<a href={item.youTube} target="_blank" rel="noopener noreferrer"> YouTube link</a>
                        </p>
                      </div>
                    </div>
                  )
                })
              }
            </div> 
         </div>
      </section>
    );
  }
}