import React, { Component } from 'react';
export default class Contact extends Component {
  render() {
    let contactData = this.props.contactData;
    return (
      <section id="contact">
        <div className="text-container">
          <div className="row">
                <ul>
                  {
                    contactData && contactData.map((item)=>{
                      return(
                        <li  key={item.name}>
                          <blockquote>
                            {<p className="contactMessage">{item.contactMessage}</p>}
                            {/*
                            <p className="contactMessage">
                              {item.contactMessage.split('\n').map( line => {return (<span >{line}<br/></span>)})}
                            </p>
                            */}
                             <p className="finishMessage">{item.finishMessage}</p>
                            <cite>{item.name}</cite>
                            <cite>{item.email}</cite>
                            <cite>{item.phone}</cite>
                          </blockquote>
                        </li>
                      )
                    })
                  }
                </ul>
          </div>
        </div>
      </section>
        );
  }
}